<template>
    <ul v-if="type == 'list'" id='FolderFullTree' class='filetree treeview'>
        <li class='expandable'>
            <div :class="{'hitarea collapsable-hitarea':organizationSpace.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' id="Organization" @click="GetFolderList('', '', 1, 'Organization Space','Organization')"
                    href='javascript:;'>Organization Space</a>
            </span>
            <ul v-if="organizationSpace.length>0" id='FolderFullTree' class='filetree treeview' style="display: none;">
                <ChildFolder v-if="organizationSpace" :routeClass="routeClass" :organizationSpace="organizationSpace" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu">
                </ChildFolder>
            </ul>
        </li>
        <li class='expandable' style="display: block;">
            <div  :class="{'hitarea collapsable-hitarea':RecentFolders.length>0}"></div>
            <span class='folder'>
                <a id="Recent" class='folderlink' @click="GetFolderList('', '', '', 'Recently Added','Recent')" href='javascript:;'>Recently
                    Added</a>
            </span>
            <ul v-if="RecentFolders.length>0" id='FolderFullTree' class='filetree treeview' style="display: none;">
                <ChildFolder v-if="RecentFolders" :routeClass="routeClass" @FolderIdMoveLeftMenu=FolderIdMoveLeftMenu :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     :RecentFolders="RecentFolders"></ChildFolder>
            </ul>
        </li>
        <li class='expandable' style="display: block;">
            <div :class="{'hitarea collapsable-hitarea':pinnedFolders.length>0}"></div>
            <span class='folder'>
                <a id="Pinned" class='folderlink' @click="GetFolderList('', '', '', 'Pinned Folders','Pinned')" href='javascript:;'>Pinned
                    Folders</a>
            </span>
            <ul v-if="pinnedFolders.length>0" class='filetree treeview' style="display: none;">
                <ChildFolder v-if="pinnedFolders" :routeClass="routeClass" @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     :pinnedFolders="pinnedFolders"></ChildFolder>
            </ul>
        </li>
        <li class='expandable' style="display: block;">
            <div  :class="{ 'hitarea collapsable-hitarea':  MySpaceFolders.length > 0}"></div>
            <span class='folder'>
                <a class='folderlink' id="MySpace"  @click="GetFolderList('', '', '', 'My Space','MySpace')">My Space</a>
            </span>
            <ul v-if="MySpaceFolders.length>0" style="display: none;">
            <ChildFolder v-if="MySpaceFolders" :routeClass="routeClass" :checkActionType="checkActionType" :type="type" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                    @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderTree="MySpaceFolders">
                </ChildFolder>
            </ul>
        </li>
        <li class="expandable" style="display: block;">
            <div  :class="{'hitarea collapsable-hitarea':sharedFolders.length>0}"></div>
            <span class='folder'>
                <a id="Shared" class='folderlink' @click="GetFolderList('', '', '', 'Shared Folders','Shared')" href='javascript:;'>Shared Folder</a>
            </span>
            <ul v-if="sharedFolders.length>0" class='filetree treeview' style="display: none;">
                <ChildFolder v-if="sharedFolders" :sharedChildFolder="sharedFolders"
                    @SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"
                     :folderTreeList="folderTreeList"
                    :userids="userid">
                </ChildFolder>
            </ul>
        </li>

    </ul>
    <ul v-else-if="type != 'list'" id='FolderFullTree1' class='filetree treeview'>
        <li v-if="type=='list' || mySpaceOrOrganization==1" class='expandable'>
            <div :class="{'hitarea collapsable-hitarea':organizationSpace.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' @click="GetFolderList('', '', 1, 'Organization Space','Organization')"
                    href='javascript:;'>Organization Space</a>
            </span>
            <ul v-if="organizationSpace.length>0" id='FolderFullTree' class='filetree treeview' style="display: none;">
                <ChildFolder v-if="organizationSpace" :organizationSpace="organizationSpace" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu">
                </ChildFolder>
            </ul>
        </li>
        <li  v-if="type=='list'" class='expandable' style="display: block;">
            <div :class="{'hitarea collapsable-hitarea':organizationSpace.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' @click="GetFolderList('', '', '', 'Recently Added','Recent')" href='javascript:;'>Recently
                    Added</a>
            </span>
            <ul class='filetree treeview'  style="display: none;">
                <ChildFolder v-if="RecentFolders" @FolderIdMoveLeftMenu=FolderIdMoveLeftMenu :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     :RecentFolders="RecentFolders"></ChildFolder>
            </ul>
        </li>
        <li  v-if="type=='list'" class='expandable' style="display: block;">
            <div :class="{'hitarea collapsable-hitarea':pinnedFolders.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' @click="GetFolderList('', '', '', 'Pinned Folders','Pinned')" href='javascript:;'>Pinned
                    Folders</a>
            </span>
            <ul class='filetree treeview' style="display: none;">
                <ChildFolder :type="type" v-if="pinnedFolders" @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     :pinnedFolders="pinnedFolders"></ChildFolder>
            </ul>
        </li>
        <li v-if="mySpaceOrOrganization!=1" class='expandable' style="display: block;">
            <div :class="{'hitarea collapsable-hitarea':MySpaceFolders.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' @click="GetFolderList('', '', '', 'My Space','MySpace')">My Space</a>
            </span>
            <ul v-if="MySpaceFolders.length>0" style="display: none;">
                <ChildFolder :checkActionType="checkActionType" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu 
                    @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderTree="MySpaceFolders">
                </ChildFolder>
            </ul>
        </li>
        <li  v-if="type=='list'" class='expandable' style="display: block;">
            <div :class="{'hitarea collapsable-hitarea':sharedFolders.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' @click="GetFolderList('', '', '', 'Shared')" href='javascript:;'>Shared Folder</a>
            </span>
            <ul class='filetree treeview' style="display: none;">
                <ChildFolder v-if="sharedFolders" :sharedChildFolder="sharedFolders" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                    @SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"
                    :SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu" :folderTreeList="folderTreeList"
                    :userids="userid">
                </ChildFolder>
            </ul>
        </li>

    </ul>
</template>
<script>
import ChildFolder from './ChildFolder.vue';
export default {
    components: { ChildFolder },
    props: {
        type: null,
        organizationSpace: null,
        // FolderIdMoveLeftMenu: {
        //     type: Function,
        //     required: false
        // },
        RecentFolders: null,
        pinnedFolders: null,
        MySpaceFolders: null,
        sharedFolders: {
            type: Array,
            required: true
        },
        type:null,
        mySpaceOrOrganization:{
            type:Number,
            required: false
        },
        checkActionType:{
            type:String,
            required: false
        },
        routeClass:{
            type:String,
            required: false
        },
        refCode:{
            type:String,
            required: false
        }
    },
    data() {
        return {
            selectedFolderClass:"selectedfolder"
        };
    },
    
    created: function(){
       var pathname = window.location.pathname;
       if(pathname.split("/")[1] == 'RecyleBin' || pathname.split("/")[1] == 'File' ||  pathname.split("/")[1]=='ViewFolderDetails' || pathname.split("/")[1]=='ViewFileDetails')
       {
        this.selectedFolderClass=""
       }
    },
    mounted: function () {
        if(this.refCode=="MySpace" && this.routeClass==undefined){
            $("#MySpace").addClass("selectedfolder");
        }
        else if(this.refCode=="Organization" && this.routeClass==undefined ){
            $("#Organization").addClass("selectedfolder");
        }
        else if(this.refCode=="Recently" && this.routeClass==undefined){
            $("#Recent").addClass("selectedfolder");
        }
        else if(this.refCode=="Pinned" && this.routeClass==undefined){
            $("#Pinned").addClass("selectedfolder");
        }
        else if(this.refCode=="Shared" && this.routeClass==undefined){
            $("#Shared").addClass("selectedfolder");
        }
        if (this.type == 'list') {
            $("#FolderFullTree").treeview();
            $("body").on("click", "#FolderFullTree .folderlink", function () {
                $("#FolderFullTree .folderlink").removeClass("selectedfolder");
                $(this).addClass("selectedfolder");
            });
        }
        else{
            $("#FolderFullTree1").treeview();
            $("body").on("click", "#FolderFullTree1 .folderlink", function () {
                $("#FolderFullTree1 .folderlink").removeClass("selectedfolder");
                $(this).addClass("selectedfolder");
            });
         
        }
    },
    methods: {
        FolderIdMoveLeftMenu: function (data, parentid, actionVal, name,routeListingcode) {
            this.$emit('FolderIdMoveLeftMenu',data, parentid, actionVal, name,routeListingcode);
        },
        GetFolderList: function (data, parentid, actionVal, name,routeListingcode) {
           
            var selectedfolder=$('.selectedfolder').attr('id');
            try {
                if(routeListingcode!=selectedfolder && selectedfolder!=undefined){
                this.$parent.$parent.$refs.listing.searchCondition ="";
            }
            } catch (error) {
                console.log(error)
            }
           
            this.$emit('Folderlisting',data, parentid, actionVal, name,routeListingcode);
        },
        SharedFolderIdMoveLeftMenu: function (data, parentid, actionVal, name, permission,routeListingcode) {
            this.$emit('SharedFolderIdMoveLeftMenu',data, parentid, actionVal, name,permission,routeListingcode);
        }
    },

}
</script>