<template>
    <div>
    <li  v-for="(item,itemIndex) in organizationSpace" class="expandable" :key="itemIndex">
        <div id="FolderFullTree"
            :class="{ 'hitarea collapsable-hitarea': hasHitAreaClass(item), 'last-hitarea': haslasthitarea(item), 'expandable-hitarea': true }">
        </div>
        <span class="folder">
            <a :id='"a"+item.FOLDER_ID' @click="FolderIds($event, item, 1, 'Organization Space','Organization')" v-if="item.folderCount > 0"
                class='folderlink myfolder' :class="{ 'folder file-pin': item.PinId > 0 }" href='javascript:;'
                :parentid="item.FOLDER_PARENT_ID" v-bind:folderId='item.FOLDER_ID'
                :OrganizationFolderIdMoveLeftMenu="OrganizationFolderIdMoveLeftMenu"
                v-bind:folderName='item.FOLDER_NAME'>{{ item.FOLDER_NAME }}</a>
                <!-- ({{ item.folderCount }}) -->
            <a :id='"a"+item.FOLDER_ID' @click="FolderIds($event, item, 1, 'Organization Space','Organization')" v-else-if="item.folderCount==0"
                class='folderlink myfolder' href='javascript:;' :parentid="item.FOLDER_PARENT_ID"
                v-bind:folderId='item.FOLDER_ID' :OrganizationFolderIdMoveLeftMenu="OrganizationFolderIdMoveLeftMenu"
                v-bind:folderName='item.FOLDER_NAME'>{{ item.FOLDER_NAME }}</a>
        </span>
        <ul style="display: none;" v-if="item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0">
            <ChildFolder :routeClass="routeClass" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu @click="FolderIds"
                v-if="item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0"
                :organizationSpace="item.chidFolder"></ChildFolder>
        </ul>
    </li>
    <li v-for="(item,index) in RecentFolders" class="expandable" :key="index">
        <div
            :class="{ 'hitarea collapsable-hitarea': hasHitAreaClass(item), 'last-hitarea': haslasthitarea(item), 'expandable-hitarea': true }">
        </div>
        <span class="folder">
            <a :id='"a"+item.FOLDER_ID' @click="FolderIds($event, item, '', 'Recently Added','Recent')" class='folderlink myfolder' href='javascript:;'
                :parentid="item.FOLDER_PARENT_ID" v-bind:folderId='item.FOLDER_ID'
                v-bind:folderName='item.FOLDER_NAME'>{{ item.FOLDER_NAME }}</a>
                <!-- ({{ item.folderCount }}) -->
        </span>
        <ul  style="display: none;" v-if="item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0">
            <ChildFolder :routeClass="routeClass" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                v-if="item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0"
                :RecentFolders="item.chidFolder"></ChildFolder>
        </ul>
    </li>
    <li v-for="(item,index1) in pinnedFolders" class="expandable" :key="index1">
        <div
            :class="{ 'hitarea collapsable-hitarea': hasHitAreaClass(item), 'last-hitarea': haslasthitarea(item), 'expandable-hitarea': true }">
        </div>
        <span class="folder">
            <a :id='"a"+item.FOLDER_ID' @click="FolderIds($event, item, '', 'Pinned Folders','Pinned')" class='folderlink myfolder' href='javascript:;'
                :parentid="item.FOLDER_PARENT_ID" v-bind:folderId='item.FOLDER_ID'
                v-bind:folderName='item.FOLDER_NAME'>{{ item.FOLDER_NAME }}</a>
                <!-- ({{ item.folderCount }}) -->
        </span>
        <ul style="display: none;"  v-if="item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0">
            <ChildFolder :routeClass="routeClass" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                v-if="item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0"
                :pinnedFolders="item.chidFolder"></ChildFolder>
        </ul>
    </li>
    <li v-for="(item,folderTreeIndex) in FolderTree" :id='"li"+item.FOLDER_ID' class="expandable" :key="folderTreeIndex">
        <div  :id='"div"+item.FOLDER_ID'  :class="{ 'hitarea collapsable-hitarea': item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0, 'last-hitarea': haslasthitarea(item), 'expandable-hitarea': true }">
        </div>
        <span class="folder">
            <a  :id='"a"+item.FOLDER_ID' @click="FolderIds($event, item, 0, 'My Space','MySpace',this)" class='folderlink myfolder'
                :class="{ 'folder file-pin': item.PinId > 0 }" href='javascript:;' :parentid="item.FOLDER_PARENT_ID"
                v-bind:folderId='item.FOLDER_ID' v-bind:folderName='item.FOLDER_NAME'>{{ item.FOLDER_NAME }}({{ item.filesCount }})</a>&nbsp;
                <!-- ({{
                        item.folderCount
                }}) -->
                <em class="fa fa-thumb-tack"  v-if="item.PinId > 0"
                                        aria-hidden="true"></em>
        </span>
        <ul style="display: none;" :id="item.FOLDER_ID" v-if="item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0" >
            <ChildFolder :checkActionType="checkActionType"  :routeClass="routeClass" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                v-if="item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0" :FolderTree="item.chidFolder">
            </ChildFolder>
        </ul>
    </li>
    <li v-for="(item,index3) in sharedChildFolder" class="expandable" :key="index3">
        <!-- <div  :class="{'hitarea collapsable-hitarea':item.hasOwnProperty('chidFolder') && item.chidFolder.length>0,'last-hitarea': haslasthitarea(item),'expandable-hitarea':true}"></div>

                <a  class='folderlink myfolder' href='javascript:;' :parentid="item.FolderId"  v-bind:folderId='item.FolderId' v-bind:folderName='item.FolderName' >{{item.OWNER_USER_NAME}}</a>  -->

        <div id="FolderFullTree"
            :class="{ 'hitarea collapsable-hitarea': item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0, 'last-hitarea': haslasthitarea(item), 'expandable-hitarea': true }">
        </div>
        <span class='folder'>
            <a @click="SharedFolderIds($event, item.PERMISSIONS, 'Shared Folders','Shared')" class='folderlink myfolder'
                href='javascript:;' v-bind:folderId='item.FOLDER_ID' :parentid="item.FOLDER_PARENT_ID"
                v-bind:permission='item.PERMISSIONS'>{{ item.FOLDER_NAME }}({{ item.OWNER_USER_NAME
                }})</a>
        </span>
        <ul id="FolderFullTree" style="display: none;" v-if="item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0">
            <ChildFolder :routeClass="routeClass" :checkActionType="checkActionType" :SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"
                v-if="item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0"
                :sharedChildFolder="item.chidFolder"></ChildFolder>
        </ul>
    </li>
    </div>
</template>
<script>
export default {
    name: 'ChildFolder',
    data() {
        return {
            checkClass:'expandable'
        }
    },
    props: {
        FolderTree: {
            type: Array,
            required: false
        },
        sharedChildFolder: {
            type: Array,
            required: false
        },
        RecentFolders: {
            type: Array,
            required: false
        },
        pinnedFolders: {
            type: Array,
            required: false
        },
        organizationSpace: {
            type: Array,
            required: false
        },
        FolderIdMoveLeftMenu: {
            type: Function
        },
        SharedFolderIdMoveLeftMenu: {
            type: Function,
            required: false
        },
        OrganizationFolderIdMoveLeftMenu: {
            type: Function,
            required: false
        },
        checkActionType:{
            type: String,
            required: false
        },
        type:{
            type: String,
            required: false
        },
        routeClass:{
            type: String,
            required: false
        }

    },
    mounted: function () {
        $("#FolderFullTree").treeview();
        $("#"+this.routeClass).addClass("selectedfolder");
            $("body").on("click", "#FolderFullTree .folderlink", function () {
                $("#FolderFullTree .folderlink").removeClass("selectedfolder");
                $(this).addClass("selectedfolder");
            });
        if (this.type == 'list') {
            $("#FolderFullTree").treeview();
            $("body").on("click", "#FolderFullTree .folderlink", function () {
                $("#FolderFullTree .folderlink").removeClass("selectedfolder");
                $(this).addClass("selectedfolder");
            });
        }
        else{
            $("#FolderFullTree1").treeview();
            $("body").on("click", "#FolderFullTree1 .folderlink", function () {
                $("#FolderFullTree1 .folderlink").removeClass("selectedfolder");
                $(this).addClass("selectedfolder");
            });
        }
    },
    methods: {
        hasHitAreaClass(item) {
            return item.hasOwnProperty('chidFolder') && item.chidFolder.length > 0
        },
        haslasthitarea(item) {
            if (item.hasOwnProperty('chidFolder')) {
                return item.chidFolder.length == 0
            } else {
                return true;
            }
        },
        // To move folder id using emit method in LeftMenu.vue
        FolderIds: function (e, item, actionVal, name,routeListingCode) {
            var vm = this;
            let aid=e.currentTarget.getAttribute("id");
            vm.FolderId = e.target.getAttribute('folderId');
            vm.FolderIdParentId = e.target.getAttribute('parentid');
            var pathname = window.location.pathname;

            if ((pathname.split("/")[1]== 'ViewFolderDetails' || pathname.split("/")[1] == 'RecyleBin' || pathname.split("/")[1] == 'ViewFileDetails') && (vm.checkActionType!='Move' && vm.checkActionType!='Copy')) {
               
                vm.$router.push({ name: 'DashBoard', params: { FolderId: vm.FolderId, RefCode: routeListingCode,ParentName:name,AddClass:aid} });
            }
            if (vm.FolderIdParentId == null) {
                vm.$emit("FolderIdMoveLeftMenu", e.target.getAttribute('folderId'), vm.FolderIdParentId, actionVal, name,aid);
            }
            else {
                vm.$parent.FolderIdMoveLeftMenu(e.target.getAttribute('folderId'), vm.FolderIdParentId, actionVal, name,aid);
            }


        },
        SharedFolderIds: function (e, permission, name, actionVal) {
            var vm = this;
            vm.FolderId = e.target.getAttribute('folderId');
            vm.FolderIdParentId = e.target.getAttribute('parentid');
            if (vm.FolderIdParentId == null) {
                vm.$emit("SharedFolderIdMoveLeftMenu", e.target.getAttribute('folderId'), vm.FolderIdParentId, actionVal, name, permission);
            }
            else {
                vm.$parent.SharedFolderIdMoveLeftMenu(e.target.getAttribute('folderId'), vm.FolderIdParentId, actionVal, name, permission);
            }

        }
    }
}
</script>